import {AfterViewInit, Component, OnInit, ViewEncapsulation} from '@angular/core';
import {AuthService} from '../../../services/auth.service';
import {ChatGroup, ChatInstance, IUser} from '../../../interfaces/interfaces';
import {FormBuilder, FormGroup} from '@angular/forms';
import {Constants} from '../../../app.constants';
import {Router} from '@angular/router';
import Swal from 'sweetalert2';
import {ChatService} from '../../../services/chat.service';
import {Subject} from 'rxjs/Rx';
import {debounceTime, distinctUntilChanged} from 'rxjs/operators';
import {AppData, ChatStatus} from '../../../data';
import * as uuid from 'uuid';
import {formatDate} from '@angular/common';

@Component({
  selector: 'app-chat',
  templateUrl: './chat.component.html',
  styleUrls: ['./chat.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class ChatComponent implements OnInit, AfterViewInit {

  searchVendorForm: FormGroup;
  selecteds = [];
  creatingGroup = false;
  loadingSearch = false;
  createConversationModal = {
    display: 1
  };
  searchText = '';
  groupFilterText = '';
  subject = new Subject<string>();
  groupSubjectFilter = new Subject<string>();

  loadingGroups = false;

  // chatInstances: ChatInstance[] = [];
  activeChat: any = false;
  errorMessage = '';
  public _this: this;

  constructor(public authService: AuthService,
              private formBuilder: FormBuilder,
              private router: Router,
              public chatService: ChatService) {
    this._this = this;
    this.subject.pipe(debounceTime(500), distinctUntilChanged()).subscribe(newValue => {
      this.searchText = newValue;
    });

    this.groupSubjectFilter.pipe(debounceTime(500), distinctUntilChanged()).subscribe(newValue => {
      this.groupFilterText = newValue;
    });
  }

  get searchedUsers() {
    return this.chatService.searchedUsers;
  }

  get isVendor() {
    return (AuthService.getAuthenticatedUser() || {}).r === AppData.roles.vendor;
  }

  get willShowNoChat() {
    return this.chatService.willShowNoChat;
  }

  get filteredChatGroup() {
    return this.chatInstances.filter(group =>
      group.group.name.toString().toLowerCase().indexOf(this.groupFilterText.toLowerCase()) > -1
    ).sort((b: ChatInstance, a: ChatInstance) => {
      const dateA = a.last_message ? a.last_message.time : a.group.time,
        dateB = b.last_message ? b.last_message.time : b.group.time;

      return (<any>(new Date(dateA)) - <any>(new Date(dateB)));
    });
  }

  get chatInstances() {
    return this.chatService.groups;
  }

  public get chatStatus() {
    return this.chatService.getConnectionStatus;
  }

  get chatStatuses() {
    return ChatStatus;
  }

  get chatList() {
    return this.chatService.groups.filter(group => group.isInit);
  }

  get vendors() {
    return this.searchedUsers.filter(user => {
      const searchText = this.searchText.toLowerCase();
      return (user.is_vendor && !this.selectedVendors.includes(user) &&
        ((user.full_name.toLowerCase().indexOf(searchText) > -1) ||
          user.vendor.business_name.toLowerCase().indexOf(searchText) > -1));
    }).sort((a: IUser, b: IUser) => {
      const aName = (a.vendor ? a.vendor.business_name : a.full_name).toLowerCase(),
        bName = (b.vendor ? b.vendor.business_name : b.full_name).toLowerCase();
      if (aName > bName) {
        return 1;
      } else if (aName < bName) {
        return -1;
      }

      return 0;
    });
  }

  get users() {
    return this.searchedUsers.filter(user => {
      return (!user.is_vendor && !this.selectedUsers.includes(user) &&
        (user.full_name.toLowerCase().indexOf(this.searchText.toLowerCase()) > -1));
    }).sort((a: IUser, b: IUser) => {
      const aName = a.full_name.toLowerCase(), bName = b.full_name.toLowerCase();
      if (aName > bName) {
        return 1;
      } else if (aName < bName) {
        return -1;
      }

      return 0;
    });
  }

  get selectedVendors() {
    return this.selecteds.filter(user => user.is_vendor).sort((a: IUser, b: IUser) => {
      const aName = (a.vendor ? a.vendor.business_name : a.full_name).toLowerCase(),
        bName = (b.vendor ? b.vendor.business_name : b.full_name).toLowerCase();
      if (aName > bName) {
        return 1;
      } else if (aName < bName) {
        return -1;
      }

      return 0;
    });
  }

  get selectedUsers() {
    return this.selecteds.filter(user => !user.is_vendor).sort((a: IUser, b: IUser) => {
      const aName = a.full_name.toLowerCase(), bName = b.full_name.toLowerCase();
      if (aName > bName) {
        return 1;
      } else if (aName < bName) {
        return -1;
      }

      return 0;
    });
  }

  ngOnInit() {
    this.searchVendorForm = this.formBuilder.group({
      description: ['']
    });
  }

  extractDate(time) {
    const date = new Date(time);

    if (this.isToday(date)) {
      return formatDate(date, 'HH:mm', 'en-US', '1');
    } else if (this.isYesterday(date)) {
      return 'Yesterday';
    }

    return formatDate(date, 'dd/MM/yy', 'en-US', '1');
  }

  isToday(someDate) {
    const today = new Date();
    return someDate.getDate() === today.getDate() &&
      someDate.getMonth() === today.getMonth() &&
      someDate.getFullYear() === today.getFullYear();
  }

  isYesterday(someDate) {
    const today = new Date();
    today.setDate(today.getDate() - 1);
    return someDate.getDate() === today.getDate() &&
      someDate.getMonth() === today.getMonth() &&
      someDate.getFullYear() === today.getFullYear();
  }

  extractLastMessage(group: ChatInstance) {
    if (!group.last_message) {
      return group.group.time_created;
    }

    const lastMessage = group.last_message, doc = document.createElement('div');
    doc.innerHTML = lastMessage.message;

    let preview = '';
    if (lastMessage.file) {
      preview = `<i>${lastMessage.file.seo_filename}</i>`;
    } else {
      preview = doc.textContent;
      // if (/<([A-Za-z][A-Za-z0-9]*)\b[^>]*>(.*?)<\/\1>/.test(lastMessage.message)) {
      //
      // } else {
      //   preview = lastMessage.message;
      // }
    }

    let creator: any = lastMessage.was_sent ? '<i class="ion ion-md-checkmark"></i>' : '<i class="ion ion-md-time"></i>';
    if ((this.chatService.presentUser || {i: ''}).i.toString() !== (group.last_message || {by: ''}).by.toString()) {
      const senders = group.group.participants.filter(user => user.id.toString() === group.last_message.by);
      if (senders.length) {
        creator = senders[0].is_vendor && senders[0].vendor ? senders[0].vendor.business_name : senders[0].full_name;
        creator += ':';
      } else {
        creator = '<i>Deleted User</i>: ';
      }
    }
    return `<strong>${creator}</strong> ` + preview;
  }

  updateSelectedVendors(vendor) {
    if (this.selecteds.includes(vendor)) {
      this.selecteds.splice(this.selecteds.indexOf(vendor), 1);
      this.searchedUsers.push(vendor);
    } else {
      this.selecteds.push(vendor);
      this.searchedUsers.splice(this.searchedUsers.indexOf(vendor), 1);
    }
  }

  updateSelectedUsers(user) {
    if (this.selecteds.includes(user)) {
      this.selecteds.splice(this.selecteds.indexOf(user), 1);
      this.searchedUsers.push(user);
    } else {
      this.selecteds.push(user);
      this.searchedUsers.splice(this.searchedUsers.indexOf(user), 1);
    }
  }

  createConversation(form) {
    const description = form.value.description.trim();
    let message = '';

    // if (this.selecteds.length > 1 && !description) {
    //     return Constants.error('Description is required for a conversation involving more than a two people.');
    // }

    if (this.selectedUsers.length > 0) {
      if (this.selectedUsers.length === 1) {
        if (this.selectedVendors.length > 1) {
          message = `You are about to create a conversation with ${this.selectedVendors.length} vendors ` +
            `and ${this.selectedUsers[0].full_name}.`;
        } else {
          message = `You are about to create a conversation with ` +
            `${this.selectedVendors[0].vendor ? this.selectedVendors[0].vendor.business_name : 'a vendor'} ` +
            `and ${this.selectedUsers[0].full_name}.`;
        }
      } else {
        if (this.selectedVendors.length > 1) {
          message = `You are about to create a conversation with ${this.selectedVendors.length} vendors and`
            + ` ${this.selectedUsers.length} other users.`;
        } else {
          message = `You are about to create a conversation with ` +
            `${this.selectedVendors[0].vendor ? this.selectedVendors[0].vendor.business_name : 'a vendor'} ` +
            `and ${this.selectedUsers.length} other users.`;
        }
      }
    } else {
      message = ``;
      if (this.selectedVendors.length === 1) {
        message = `You are about to create a conversation with ${this.selectedVendors[0].business_name}.`;
      } else {
        message = `You are about to create a conversation with ${this.selectedVendors.length} vendors.`;
      }
    }

    message += '<br>Click Continue to proceed.';

    Swal.fire({
      title: 'Confirmation',
      html: message,
      type: 'question',
      cancelButtonText: 'Cancel',
      showCancelButton: true,
      confirmButtonText: 'Continue',
      showConfirmButton: true
    }).then(goOn => {
      if (goOn.value) {
        this.creatingGroup = true;
        const d = {participants: [], description: description, signature: uuid.v4()};
        this.selecteds.forEach(user => d.participants.push(user.id));
        this.chatService.createGroup(d).subscribe((data) => {
          // $('#chat-create-conversation-modal').modal('hide');
          Constants.succeeded('New conversation created.');
          // switch to conversation box
          this.loadingSearch = false;
          const sels = this.selecteds;
          this.selecteds = [];
          sels.forEach(sel => {
            this.searchedUsers.push(sel);
          });
          this.createConversationModal.display = 1;
          document.getElementById('close-modal-button').click();
          this.creatingGroup = false;
        }, (err) => {
          this.errorMessage = Constants.handleError(err, this.router, true);
          this.creatingGroup = false;
        })
      }
    });
  }

  showConversation(group: ChatInstance) {
    this.chatService.willShowNoChat = false;
    if (this.activeChat) {
      this.activeChat.show = false;
    }

    this.chatService.showChatList = false;

    this.activeChat = group;
    this.activeChat.show = true;
    this.activeChat.isInit = true;
    if (group.unreadCount) {
      setTimeout(() => {
        if (group.show) {
          group.unreadCount = 0;
        }
      }, 1500);
    }
  }

  private listenForNewGroup() {
    // const channel = this.chatService.pusher.subscribe('private-chat-group');

    // channel.bind('pusher:subscription_succeeded', (data) => {
    //     channel.bind('App\\Events\\ChatGroupEvent', (chatObj) => {
    //         if (chatObj && chatObj.group) {
    //             if ((chatObj.group.participants || []).filter(participant => participant.id.toString()
    //                 === this.presentUser.id.toString()).length) {
    //                 this.chatInstances.push(chatObj.group);
    //             }
    //         }
    //     });
    // });
  }

  ngAfterViewInit() {
    document.getElementById('preloader').classList.add('hide');
  }
}
