import {Component, Input, OnInit, ViewChild, ViewEncapsulation} from '@angular/core';
import {ChatInstance} from '../../../interfaces/interfaces';
import {UploadService} from '../../../services/upload.service';
import {ChatService} from '../../../services/chat.service';
import {Router} from '@angular/router';
import {ChatStatus} from '../../../data';
import {AppNotifierService} from '../../../services/app-notifier.service';
import {LightBoxComponent} from '../../../shared/upload/light-box/light-box.component';

@Component({
    selector: 'app-chat-message',
    templateUrl: './chat-message.component.html',
    styleUrls: ['./chat-message.component.scss'],
    encapsulation: ViewEncapsulation.None
})
export class ChatMessageComponent implements OnInit {

    @Input() public chat: ChatInstance;
    upload: any = false;
    nextId = 0;
    @Input() loadingPresentUser = false;
    @Input() loadingPresentUserError = '';

    display = 'chat';
    searchText = '';

    @ViewChild(LightBoxComponent, {static: true}) lightBox: LightBoxComponent;

    constructor(private uploadService: UploadService,
                public chatService: ChatService,
                private router: Router) {
    }

    ngOnInit() {}

    get presentUser() {
        return this.chat.presentUser;
    }

    get nextUrl() {
        return this.chat.nextUrl;
    }

    set nextUrl(url) {
        this.chat.nextUrl = url;
    }

    get filteredGroups() {
        return this.chatService.groups.filter(ins => {
            return (ins.group.name.toString().toLowerCase().includes(this.searchText.toLowerCase())
                && ins.group.id !== this.chat.group.id);
        });
    }

    openMedia(mes): void {
        const media = this.mediaMessages;
        const selection = media.findIndex(m => m.id === mes.id);
        const index = selection > -1 ? selection : 0;
        if (index < 0) return;
        this.lightBox.open(index);
    }

    get mediaMessages() {
        return this.chat.sortedMessages.filter(mes => {
            return mes.file && mes.file.path;
        });
    }

    get statuses() {
        return ChatStatus;
    }
}
